import Element from './Element'
import { useState, useEffect} from 'react'

const Elements = ({ works , updateElement }) => {
  

const [worksFilter, setWorksFilter] = useState( works )
const onChange = async () =>{
	const x = document.getElementById("selectivo").value;

	if(x == "Todas"){
		setWorksFilter(works);
	}else{
		let new_array =  works.filter(works => works.Technique == x);  
    	setWorksFilter(new_array);
    }	 
}

useEffect(() =>{
    const getFilter= () =>{
        onChange() 
    }
    getFilter()
}, [works])



   
    
    return (
    	<>
    	<div className = "tecnicas">
    	<label>Técnicas   </label>
    	<select onChange = {onChange} id="selectivo">
       			<option defaultValue ="Todas">Todas</option>
       			<option value ="Digital">Digital</option>
       			<option value ="Acrílicos">Acrílicos</option>
       			<option value ="Otras Técnicas">Otras Técnicas</option>
       		</select>
       	</div>
       		
        <div className= "galerie">    		

        {worksFilter.map((element) =>(	
         <Element key = {element.id} element ={element} 
         	 updateElement = {updateElement}
         />
    
    ))}
 		<div id = "overBar" className= "galeriaElemento" >
 			<p style ={{width: "100%", textAlign: "center"}}>Holasdasd asdasdasdasd</p>
 		</div>	
        </div>
        </>
    )
}

export default Elements