import React from 'react';


const Elements = ({ newElement, hide}) => {
    
   const ver = () =>{
   			console.log(newElement);
   }

   ver();
    
    return (
      <div className="ventana">
      	<div className="ventana_cerrar" onClick = {() =>hide(false)} >X</div>
 
          		<div className = "ventana_seccion"><img style = {{width: "100%"}} src= {`../images/${newElement.url}`} /></div>
      			<div className = "ventana_seccion">
      	  			<div className = "ventana_title"><h1>{newElement.title}</h1></div>
      	  			<h3>{`Técnica: ${newElement.Technique}` }</h3>
      	  			<p>{newElement.Comentarios}</p>
      	  		</div>
 

    	
    </div>	
    )
}

export default Elements