import { useState, useEffect } from 'react'
import Header from './Components/Header';
import Elements from './Components/Elements';
import NewWindow from './Components/NewWindow';
import './App.css';

function App() {
  const [works, setWorks] = useState([])
  const [newElement, setElement] = useState([]);
  const [showElement,setShowElement] = useState (false) 

  useEffect(() =>{


    const getTasks= async () =>{
        const tasksFromServer = await fetchTasks()
        setWorks(tasksFromServer)
    }
    getTasks()

  }, [])

  const updateElement=  (element) =>{
    setShowElement(true);
    setElement(element);
  } 

  const fetchTasks = async () => {
            const res = await fetch ('http://localhost:4000/ilustrations')
            const data = await res.json()
        return data;
  }
 

  return (
    <div>
      <Header />
      {showElement &&<NewWindow newElement = { newElement }  hide = {setShowElement}/>}
      <Elements works = { works }  updateElement = {updateElement}/>

    </div>
  );
}

export default App;
