import { useState, useContext } from 'react'

const Element = ( { element, updateElement}) => {


const changeBackground = () => {
	const div = document.getElementById("overBar");
	div.style.display="block";
	div.innerHTML = '<p>' + element.title + "</p>";

	const divElemento = document.getElementById("Elemento" + element.id);
	const x = divElemento.getBoundingClientRect().left
	const y = divElemento.getBoundingClientRect().top
	console.log(div.style.top)
	div.style.top = y + "px";
	console.log(div.style.top)
	div.style.left = x+ "px";
}

const leaveElement = () =>{
	const div = document.getElementById("overBar");
	div.style.display="none";
}
 	
		
    return (
    
        <a id = {`Elemento${element.id}` } 
        	onClick = {() =>updateElement(element)} 
        	className= "galeriaElemento"
        	onMouseEnter={changeBackground}
        	onMouseLeave={leaveElement}>
            <img width = "100%" height= "auto" src= {`../images/${element.url}` } alt = {element.title}/>
        </a>
    )

    }



export default Element